<template>
   <div class="dateTimer" :style="{width: timeOpt.width || '7.5em'}">
   	  <button class="time-button" @click="showTimeSelect"   >
   	    {{time}}
   	    <i class="hasIcon" style="float:right;margin-right:-0.15em;margin-top:-1px;font-size:1.3em"><icon name="clock-o"></icon></i>
   	  </button>
      <div class="myTimer" v-show="showTimeCon" >
      	   <div class="timers">
      	   	  <div class="timers-slipt">
      	   	  	 <span>小时</span>
      	   	  	 <VuePerfectScrollbar class="timerCon noRightBorder">
      	   	  	 	<li v-for="(item, $index) in getCon('hour')"   :class="{selectedLi: item.value === selectHour.value}" @click="getHour(item)">
      	   	  	 		<span>{{item.text}}</span>
      	   	  	 	</li>
      	   	  	 </VuePerfectScrollbar>
      	   	  </div>
      	   	  <div class="timers-slipt">
      	   	  	 <span>分钟</span>
      	   	  	 <VuePerfectScrollbar class="timerCon">
      	   	  	 	<li v-for="(item, $index) in getCon('min')" :class="{selectedLi: item.value === selectMin.value}" @click="getMin(item)">
      	   	  	 		<span>{{item.text}}</span>
      	   	  	 	</li>
      	   	  	 </VuePerfectScrollbar>
      	   	  </div>
      	   	  <div class="timers-slipt">
      	   	  	 <span>秒数</span>
      	   	  	  <VuePerfectScrollbar class="timerCon noLeftBorder" >
      	   	  	  	<li v-for="(item, $index) in getCon('second')"  :class="{selectedLi: item.value === selectSecond.value}" @click="getSen(item)">
      	   	  	  		<span>{{item.text}}</span>
      	   	  	  	</li>
      	   	  	  </VuePerfectScrollbar>
      	   	  </div>
      	   </div>
      	   <div class="footer-operate">
      	   	   <button @mousedown="clearTime">清空</button>
      	   	   <button @mousedown="getTimeNow">现在</button>
      	   	   <button @mousedown="cancelTime">取消</button>
      	   	   <button @mousedown="submitTime">确定</button>
      	   </div>
      </div>
   </div>
</template>
<script>
import 'vue-awesome/icons/clock-o'
import prefectScroll from 'perfect-scrollbar'
 export default {
   data() {
   	 return {
       time: '--:--:--',
       selectHour: '',
       selectMin: '',
       selectSecond: '',
       showTimeCon: false
   	 }
   },
   props: ['timeOpt'],
   methods: {
   	  showTimeSelect: function () {
        this.showTimeCon = !this.showTimeCon;
   	  },
      clearTime: function() {
        this.selectHour = '';
        this.selectMin = '';
        this.selectSecond = '';
        this.time = '--:--:--'
      },
      getFormatter: function (val) {
      	 if(val == '--') {
      	 	return '';
      	 } else if(parseInt(val) < 10) {
         	let t = "0" + parseInt(val);
            return {text: t, value: t}
         }
         return {text: val, value: val}
      },
      getTimeNow: function() {
        let now = new Date();
        this.selectHour =  this.getFormatter(now.getHours());
        this.selectMin =  this.getFormatter(now.getMinutes());
        this.selectSecond = this.getFormatter(now.getSeconds());
        this.getSelectTime();
      },
      getSelectTime: function () {
         this.time = (this.selectHour.value || '--') + ':' + (this.selectMin.value || '--') + ':' + (this.selectSecond.value || '--') 
      },
      cancelTime: function() {
         this.showTimeCon = false;
         let arr = this.time.split(':');
         this.selectHour = this.getFormatter(arr[0]);
         this.selectMin = this.getFormatter(arr[1]);
         this.selectSecond = this.getFormatter(arr[2]);
      },
      submitTime: function() {
        this.getSelectTime();
        this.showTimeCon = false;
        this.$emit('getSelectTime',{key: this.timeOpt.key, val: this.time === '--:--:--' ? '' : this.time, obj: this.$parent});
      },
   	  //获取小时分钟秒的数据
   	  getCon:function (type) {
   	  	let len;
   	  	let i = 0;
        switch(type) {
           case 'hour':
             len = 24;
             i = 0;
             break;
           case 'min':
             len = 60;
             i = 0;
             break;
           case 'second': 
             len = 60;
             i = 0;
             break;  
        }
        let arr = [];
   	  	for( i ; i< len; i++) {
   	  	   let h = i < 10 ? ('0' + i) : i;
           arr.push({text: h, value: h})
   	  	}
   	  	return arr
   	  },
   	  getHour: function (item) {
        this.selectHour = item
   	  },
   	  getMin: function (item) {
        this.selectMin = item
   	  },
   	  getSen: function (item) {
        this.selectSecond = item
   	  },
   },
   created: function () {
   	 if(typeof this.timeOpt.value !== 'undefined' &&  this.timeOpt.value !== '') {
   	 	 this.time = this.timeOpt.value;
   	 	 this.cancelTime();
   	 }
    },
    mounted: function () {
    	if(typeof this.timeOpt.value === 'undefined') {
            this.getTimeNow();
         	this.submitTime();
    	}
    } 
 }
</script>
<style lang="scss">
@import "../../assets/main.scss";
$theColor: #227fdf;
   .dateTimer{
      width: 15em;
      float:left;
      position: relative;
      .time-button {
      	  min-width: 7em;
      	  width:100%;
          float: left;
          text-align: left;
          position: relative;
          border-radius:0.2em;
          border:1.5px solid $theColor;
          line-height:1.5;
          background: rgba($theColor, 0.2);
          color:white;
          span{
              position: absolute;
              right: 9px;
              top: 9px
          }
      }
      .myTimer {
          font-size:0.9em;
          outline: none;
          width:100%;
          height:18em;
          position: absolute;
          border: 1px solid #305ea0;
          top: 2.8em;
          border-radius: 4px;
          background: rgba(#305ea0, 1);
          color:white;
          z-index: 11;
          .timers {
              width:calc(100% - 1.6em);
              height:calc(100% - 3em - 1px - 1.6em);
              padding: 0.8em;
              border-bottom:1px solid rgba(255,255,255,0.3);
              .timers-slipt{
              	width: calc((100% - 4px) / 3);
              	height: calc(100% - 0.5em);
                font-size:0.94em;
              	float:left;
              	 text-align:center;
              	.timerCon{
                   width:calc(100% - 1px);
                   height: calc(100% - 2.5em);
                   position:relative;
                   margin-top:0.8em;

                   border: 1px solid rgba(255,255,255,0.3);
                   li{
                   	min-width: 2em!important;
                   	width:100%;
                   	padding: 0.4em 0;
                   	color:white;
                   	cursor: pointer;
                   	margin: 0;
                   	text-align:center;
                   }
                   li:hover,li.selectedLi{
                   	 background: $theColor;
                   }
                }
                .noRightBorder{
                	border-right-color:transparent;
                	li:hover,li.selectedLi{
                      // border-right: 1px solid rgba(255,255,255,0.3);
                	}
                }
                .noLeftBorder{
                	border-left-color:transparent;
                	li:hover,li.selectedLi{
                	  //border-left: 1px solid rgba(255,255,255,0.3);
                	}
                }
              }
          }
          .timers:after{
          	 @extend .clearElement
          }
          .footer-operate{
          	width: fit-content;
          	height:3em;
            float: right;
            padding-right:4%;
            font-size:0.83em;
          	button{
               padding:4px 8px;
               line-height:1.8;
               height:2.3em;
               margin: calc(((3em / 0.83) - 2.3em) / 2) 0.5px;
               float: left;
               border-radius:0;
               font-size:1em;
          	}
          	button:hover{
          		color: #fbd63f
          	}
          	button:first-child{
          		border-top-left-radius:0.3em;
          		border-bottom-left-radius:0.3em
          	}
          	button:last-child{
          		border-top-right-radius:0.3em;
          		border-bottom-right-radius:0.3em
          	}
          }
      }
   }
   .dateTimer:after{
   	  @extend .clearElement
   }
   @media screen and (max-width: 1680px) {
    .dateTimer .myTimer .footer-operate button{
    	margin: calc(((3.2em) - 2.3em) / 2) 0.5px;
    }
  }
   @media screen and (max-width: 1440px) 
   {
     padding:4px;
   }

  
</style>


