<template>
  <div ref="trys" style="height: calc(100%)">
    <div class="all-container noback-Container">
      <div class="con-left leftTree theTree" >
          <inputs ref="inputs"  :option="searchInput" style="margin:0 0.65em "
                         @getInput="getFrom"></inputs>
          <VuePerfectScrollbar class="treeCon">
                   <tree  v-if="dataReady" ref="myTree"  :noCheck="{'key': 'nodeType', 'value': '1'}" :treeOpt="treeOpt" :isInit="true" @getNode="getNodes"  ></tree>
          </VuePerfectScrollbar>
          <div class="submitCon">
            <p>已选择 <span style="color:#FBD63F">{{selectedNode.length}}</span> 只设备</p>
            <button @click="submitDevice">确定</button>
          </div>
        </div>
      <div class="con_center">
          <ul class="form-list detail-list leftInfo" :class="{maxLeftWidth: scheme.schemeType != 2}">
            <li v-for="(item,$index) in commInfoL" :class="item.classes" >
              <span v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span> {{item.label}} </span>
              <span v-if="item.isEmpty" class="label-show"><span >&nbsp;</span>{{item.label}} </span>
              <inputs v-if="item.type === 'input'"  :option="item.options" @getInput="getFrom" ></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" ></selects>
              <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
            </li>
          </ul>
           <ul class="form-list detail-list rightInfo" v-if="scheme.schemeType == 2">
            <li v-for="(item,$index) in commInfoR" class=" formList " :class="item.classes">
               <span class="label-show" style="min-width:7.5em">{{item.name}}</span>
               <datePicker :ref="item.option[0].key" :dateOpt="item.option[0]" @getDatePicker="getFrom" :showAfterDisabled="false" ></datePicker>
               <dateTimer :ref="item.option[1].key" :timeOpt="item.option[1]" @getSelectTime="getFrom" style="margin-left:1em"></dateTimer>
            </li>
          </ul>
          <div ref="myOwnTable" class="table-container" :style="{height: 'calc(100% - 7em)'}">
             <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"></tables>
         </div>

      </div>
      <div class="con_bottom">
         <button @click="cancelPlan" class="cancel-button">
           <i class="hasIcon"><icon name="mail-reply" scale="1.3"></icon></i>
           重置
         </button>
         <button @click="submitPlan" >
            <i class="hasIcon"><icon name="save" scale="1.4"></icon></i>
            提交
         </button>
    </div>
    </div>

    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
import dateTimer from '../childComponent/DateTimer.vue'
import 'vue-awesome/icons/mail-reply'
import tree from '../childComponent/myTree.vue'
export default {
  data() {
    return {
      showTable: false,
      searchParam: { //查询table的参数对象
        deviceNo: '',
        deviceType: '07',
        isImportant:0
      },
      dataReady: true, //重载树组件
      clickEvent: true, //阻止单击事件重复提交
      showDialog: true,
      scheme: {
        schemeName: "",
        schemeType: "1",
        startDate: "",
        startTime: "",
        endDate: "",
        endTime: "",
        remark: "",
        Date: "",
        meterList: [
        ]
      },
      TableOpt: {
          column: [
              {title: '用户户名', field: 'customerName', textAlign: 'center', width: '80'},
              {title: '用户户号', field: 'meterUserNo', textAlign: 'center', width: '70'},
              {title: '用户表号', field: 'deviceNo', textAlign: 'center', width: '80'},
              {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '80'},
              {title: '楼栋', field: 'buildingNo', textAlign: 'center', width: '80'},
              {title: '变压器', field: 'transformerNo', textAlign: 'center', width: '80'},
              {title: '操作', field: '', textAlign: 'center', width: '70',
              type:"object",
              formatter: function (val, index, row, obj) {
                  let template;
                  template = '<button class="list-operate"  @click="openAccount">' + '  移除</button>';
                  return {
                    template: template,
                    methods: {
                      openAccount: function () {
                        obj.$parent.removeDevice(row);
                      }
                    }
                  }
                }

              },
          ],
          data:[],
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60]
      },
      selectedNode: []//选中的设备
    }
  },
  components: {
    tree,
    dateTimer
  },
  computed: {
    searchInput: function ()  {
        return {
           width: 'calc(100% - 1.3em)' ,
           type: 'typeSearch',
           key: 'searchParam.deviceNo',
           value: this.searchParam.deviceNo,
           placeholder: '请输入设备号',
           hideSelect: true
         }
    },
    treeOpt: function () {
      let url = "tree/queryMainSubTree";
      if(this.searchParam.deviceNo !== '') {
          url = "tree/fuzzyQueryTree";
      }
      return  {
               init: true,
               url: url,
               method: 'post',
               param: this.getSearchData(this.searchParam),
               searchVal: [this.searchParam.deviceNo],
               childUrl: 'tree/queryMainSubTree'
          }
    },
    commInfoL: function() {
      let width = 'calc(100% - 8em)';
      return [
        {
          classes: 'halfWidth',
          type: 'select',
          label: '方案类型',
          isEmpty: false,
          options: {
              key: 'scheme.schemeType',
              cs: 'server',
              url: 'dictItem/queryByDictCode',
              method: 'post',
              param: {dictCode: '16'},
              text: 'dictItemName',
              value: 'dictItemValue',
              constant: this.scheme.schemeType !== '' ? '' : {dictItemValue: this.scheme.schemeType},
              width: width
          }
        },
        {
          classes: 'halfWidth hasMarginLi',
          type: 'input',
          isEmpty: false,
          label: '方案名称',
          options: {
            type: 'normalInput',
            key: 'scheme.schemeName',
            value: this.scheme.schemeName,
            width: width,
            placeHolder: '请输入方案名称',
            length: 50
          }
        },
        {
          classes: 'allWidth ',
          type: 'input',
          isEmpty: true,
          label: '备注信息',
          options: {
            type: 'textArea',
            width: 'calc(100% - 8.2em)',
            placeHolder: '200个字符内',
            value: this.scheme.remark,
            key: 'scheme.remark',
            length: 200
          }
        }
      ];
    },
    commInfoR: function () {
        let widths = 'calc((100% - 9em)/2)';

        return [
        {
                      name: '通电时间',
                      style: 'timerCombine',
                      unShow: this.scheme.schemeType != '2',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: [{
                            key: 'scheme.startDate',
                            width: widths,
                            value: this.scheme.startDate,
                            hasRemove: true
                         },
                         {
                            key: 'scheme.startTime',
                            width: widths,
                            value: this.scheme.startTime
                         }
                      ]
                   },
                   {
                      name: '断电时间',
                      style: 'timerCombine',
                      unShow: this.scheme.schemeType != '2',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: [{
                            key: 'scheme.endDate',
                            width: widths,
                            value: this.scheme.endDate,
                            hasRemove: true
                         },
                         {
                            key: 'scheme.endTime',
                            width: widths,
                            value: this.scheme.endTime
                         }
                      ]
                   }
       ]
    }
  },
  methods: {
    cancelPlan: function () {
       this.$refs.myTable.resetTableOpt({data: []});
       this.scheme = this.cleanSearchData(this.scheme);
       this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'isImportant']);
       this.dataReady = false;
       let that = this;
       that.$nextTick(function () {
           that.dataReady = true;
       });
       this.selectedNode = [];
    },
    //移除某个设备
    removeDevice: function (row) {
       let arr = [];
       arr.push(row);
       this.$refs.myTable.operateData(arr, 'cno', 1);
    },
    //获取树形节点
    getNodes: function (val) {
      this.selectedNode = [];
      let arr = [];
      for(let i=0;i<val.length;i++) {
          arr.push(val[i].nodeId);
      }
      this.selectedNode = arr;
    },

    //提交设备数据
    submitDevice: function() {
      let that = this;
      if(that.selectedNode.length == 0){
         that.$refs.alerts.show({
                msg: '请先选择设备！'
         });
         return
      }
      that.getRequest({
         url: 'scheme/queryByCnos',
         method: 'post',
        // url: 'dist/json/planList1.json',
       //  method: 'get',
         param: {'cnos': that.selectedNode},
         success: function(data) {
            that.setTableData(data.data);
         },
         error: function(data) {
            that.$refs.alerts.show({
                msg: data.message
             });
            }
        }, true)
    },
    submitPlan: function () {
       let that = this;
       if(that.scheme.schemeName === ''){
         that.$refs.alerts.show({msg: '方案名称不能为空！'});
         return;
       }
       if((that.scheme.startDate != '' && that.scheme.startTime !== '') || (that.scheme.endDate != '' && that.scheme.endTime !== '') || this.scheme.schemeType != '2') {
         let param = this.getSearchData(this.scheme);
         let data = this.$refs.myTable.getAllData();
         if(data.length === 0) {
             that.$refs.alerts.show({msg: '设备列表不能为空！'});
         } else {
           let arr = [];
           for(let i =0; i<data.length; i++) {
             let item = data[i];
             arr.push({customerNo: item.customerNo, deviceNo: item.deviceNo, cno: item.cno, meterUserNo: item.meterUserNo})
           }
           param.meterList = arr;
           that.getRequest({
             url: 'scheme/add',
             method: 'post',
             param: param,
             success: function (data) {
                that.cancelPlan();
                that.$refs.alerts.show({msg: data.message});
              },
             error: function (data) {
              that.$refs.alerts.show({msg: data.message});
             }
           })
        }
      } else {
        that.$refs.alerts.show({msg: '通电时间或者断电时间至少填写一项'});
      }
    },
    //刷新表格数据
    setTableData: function (data) {
       this.$refs.myTable.operateData(data, 'cno', 0);
    },
    getFrom: function(val) {
       if(val.key == 'searchParam.deviceNo' && this.searchParam.deviceNo !== val.val) {
             this.getOption(val, true);
              this.dataReady = false;
              let that = this;
              that.$nextTick(function () {
                that.dataReady = true;
              });
        } else {
          this.getOption(val, true);
        }
    },
    windowChange: function() {
      let that = this;
      that.getHeight('calc(100% - 0em)');
     /* window.onresize = function() {
        that.getHeight('calc(100% - 0em)');
      } */
    },
    init: function() {
      let that = this;
      /*window.onresize = function() {
        that.windowChange()
      }*/
    },
    cancelRight(){
      this.$refs.myTable.resetTableOpt({data: []});
      this.scheme = this.cleanSearchData(this.scheme);
      this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'isImportant']);
    }
  },
  created: function() {
    // this.TableOpt.column = this.columns;
  },
  mounted: function() {
    let that = this;
    that.isIn();
    that.getAction();
  },
  watch: {

  }
}

</script>
<style lang="scss">
@import "../../assets/main.scss";
.right-container .center-container .noback-Container {
  background: transparent;
  padding: 0;
  width: 100%;
}
.halfWidth {
    //width: calc(20% - 3em)!important;
    margin: 0.2em!important;
    min-width: 18em;
}
.theTree{
 font-size:0.83em;
  width: 16%;
  .treeCon{
    margin:0.65em;
    width:calc(100% - 1.3em);
    height:calc(100% - 6em);
    position:relative;
    float:left;
  }
  .submitCon{
   float:left;
   padding-left:1em;
   width:calc(100% - 1em);
   p{
     margin:0;
     float:left;
     line-height: 3
   }
   button{
      font-size: 0.875em;
      padding: 0.4em 1em;
      float: right;
      margin: 0.5em 1em;
      height: 2.4em;
   }
  }
  .submitCon:after{
     @extend .clearElement
  }
}
.theTree + .con_center{
  width: calc(84% - 3.4em);
  height:calc(100% - 5.5em);
  //padding: 0!important;
 // background:transparent;
  .leftInfo{
    width:60%;
    float:left;
    .halfWidth{
      width: calc(50% - 1.6em)!important;
      margin: .2em!important
    }
    .hasMarginLi{
      margin: .2em .2em .2em 2em!important
    }
  }
  .maxLeftWidth{
    width:80%;
  }
  .rightInfo{
    width: calc(40% - 2em);
     float:left;
     margin-top:0.2em
  }
  .maxLeftWidth + .rightInfo{
     width: 0;
     display: none
  }
  .leftInfo:after,.rightInfo:after{
      @extend .clearElement
  }
}
.con_bottom{
    text-align: center;
    float: left;
    width: calc(84% - 0.4em);
    border-radius: 0.3em;
    margin-top: 0.4em;
    margin-left: 0.4em;
    height:3.5em;
    background: rgba(53, 136, 250, 0.3);
    button{
      font-size:0.83em;
      padding:0.6em 1.8em;
      margin:0.65em ;
   }
}

</style>



